import React, { useState, useCallback } from "react";
import { useNavigate } from 'react-router-dom'
import { request } from "../../lib/request";
import { RegisterForm } from "../../styles/styles";

const RegistPage = () => {
  const navigate = useNavigate();
  
  const [userInfo, setUserInfo] = useState({
    email: {
      value: "",
      msg: "",
    },
    password: {
      value: "",
      msg: "",
    },
    passwordCheck: {
      value: "",
      msg: "",
    },
  });

  const inputChange = useCallback((e) => {
    const { name, value } = e.target;

    let msg = '';
    if(value === '')
      msg = (name === 'email') ? '  .' : '  .';
    else if(name === 'email' && !/\S+@\S+\.\S+/.test(value))
      msg = '  .';
    else if((name === 'password' || name === 'passwordCheck') && value.length < 8)
      msg = '8  .';
    else if(name === 'pwdCheck' && userInfo.pwd.value !== value)
      msg = '  .';
      
    setUserInfo({
      ...userInfo,
      [name]: {
        ...userInfo[name],
        value: value,
        msg: msg,
      },
    });
  }, [userInfo]);

  const onRegisterSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await request.post("/auth/signup", {
        email: userInfo.email.value,
        password: userInfo.password.value
      });

      localStorage.setItem('accessToken', res.data.access_token);
      alert('   .');
      navigate('/');
    } catch(e) {
      console.log(e);
      alert(e.response.data.message);
    }
  };

  return (
    <RegisterForm>
      <form onSubmit={onRegisterSubmit}>
        <h1></h1>
        <input type="text" name="email" onChange={inputChange} placeholder=" " required/>
        <input type="password" name="password" onChange={inputChange} placeholder="" required/>
        <input type="password" name="passwordCheck" onChange={inputChange} placeholder=" " required/>
        <button type="submit"> </button>
      </form>
    </RegisterForm>
  );
};

export default RegistPage;
